<template>
  <ul class="songs-wrapper">
		<li class="item" flex>
			<div flex-item="1"></div>
			<div flex-item="1"></div>
			<div flex-item="7">歌曲</div>
			<div flex-item="2">歌手</div>
			<div flex-item="2">专辑</div>
			<div flex-item="2"></div>
		</li>
		<li v-for="(item, index) in songs" flex :key="index" class="item light">
			<div flex-item="1" class="center">{{ index }}</div>
			<div flex-item="1"><img src="../assets/images/wave.gif" v-if="songIdx == index && (fullpath == pagePath || pathName === 'now')"></div>
			<div flex-item="7">{{ item.song_name }}</div>
			<div flex-item="2">{{ item.singer }}</div>
			<div flex-item="2">{{ item.album_name }}</div>
			<div flex-item="2" class="icon">
        <i class="list-menu-icon" @click="play(index)" :class="[((songIdx == index && (fullpath == pagePath || pathName === 'now')) && playStatus === true) ? 'pause-icon' : 'play-icon']"></i>
        <i class="list-menu-icon add-icon" @click="add(item.song_id)" v-if="pathName !== 'list'"></i>
        <i class="list-menu-icon remove-icon" @click="remove(item.song_id)" v-if="pathName === 'list'"></i>
      </div>
		</li>
	</ul>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'index',
  props: {
    songs: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data () {
    return {
      pathName: '',
      fullpath: ''
    }
  },
  created () {
    this.pathName = this.$route.name
    this.fullpath = this.$route.fullPath
    if (this.songArr === null) {
      this.handleSetPath(this.fullpath)
      this.handleSetSongArr(this.songs)
    }
  },
  computed: {
    ...mapState('player/song', [
      'songIdx', 'playStatus', 'songArr', 'pagePath'
    ])
  },
  methods: {
    // 播放音乐
    play (index) {
      this.handleSetSongArr(this.songs)
      if (index !== parseInt(this.songIdx)) {
        this.handleSetSongIdx(index)
      } else {
        this.handleSetPlayStatus(!this.playStatus)
      }
      if (this.pathName !== 'now') {
        this.handleSetPath(this.fullpath)
      }
    },
    // 添加音乐
    async add (id) {
      const params = {
        ids: id
      }
      const res = await this.$http.post('/user/song/addplaylist', params)
      if (res.status === true) {
        alert('添加列表成功')
      } else {
        alert(res.msg)
      }
    },
    // 删除音乐
    async remove (id) {
      const params = {
        ids: id
      }
      const res = await this.$http.post('/user/song/delplaylist', params)
      if (res.status === true) {
        location.reload()
      } else {
        alert(res.msg)
      }
    },
    ...mapActions('player/song', [
      'handleSetSongIdx', 'handleSetPlayStatus', 'handleSetSongArr', 'handleSetPath'
    ])
  }
}
</script>

<style scoped lang="scss">
.songs-wrapper {
  .item {
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    color: #efefef;
    border-bottom: 1px solid rgba(200, 200, 200, .1);
    &.light {
      opacity: .8;
    }
    &:hover {
      .list-menu-icon {
        display: inline-block;
      }
    }
  }
}
.list-menu-icon {
  display: none;
  width: 36px;
  height: 36px;
  background-image: url('../assets/images/icon_list_menu.png');
  background-repeat: no-repeat;
  cursor: pointer;
  &.play-icon {
    margin: 7px 0 0;
    background-position: -80px 0;
    &:hover {
      background-position: -120px 0;
    }
  }
  &.pause-icon {
    margin: 7px 0 0;
    background-position: -80px -200px;
    &:hover {
      background-position: -120px -200px;
    }
  }
  &.add-icon {
    margin: 7px 6px 0;
    background-position: -80px -80px;
    &:hover {
      background-position: -120px -80px;
    }
  }
  &.remove-icon {
    margin: 7px 6px 0;
    background-position: -80px -160px;
    &:hover {
      background-position: -120px -160px;
    }
  }
}
.center {
  text-align: center;
}
</style>
