<template>
  <div class="wrapper">
    <PlayList :songs=songs v-if="songs.length !== 0"></PlayList>
  </div>
</template>

<script>
import PlayList from '../../../components/play-list'
export default {
  name: 'index',
  components: {
    PlayList
  },
  data () {
    return {
      songs: []
    }
  },
  created () {
    this.getList()
  },
  methods: {
    // 喜欢音乐列表
    async getList () {
      const res = await this.$http.post('/user/song/lovelists', {})
      if (res.data) {
        let arr = []
        for (let i in res.data) {
          arr.push(res.data[i])
        }
        this.songs = arr
      }
    }
  }
}
</script>

<style scoped lang="scss">
.wrapper {
  margin: 30px 0 0;
  height: calc(100% - 66px);
  overflow: hidden;
  overflow-y: auto;
}
</style>
